.sidebar {
    background-color: rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    padding: 6rem 0;
    
  }
  
  .sidebar > a:first-child {
    padding: 0;
    margin-top: 3vmax;
  }
  .sidebar > a > img {
    width: 100%;
    transition: all 0.5s;
  }
  
  .sidebar > a > img:hover {
    filter: drop-shadow(0 0 10px "rgb(155,121,102)");
  }
  .sidebar a {
    text-decoration: none;
    color: rgb(30, 27, 27);
    font: 200 1rem Poppins;
    padding: 2rem;
    transition: all 0.5s;
  }
  .sidebar a:hover {
    color: "rgb(155,121,102)";
    transform: scale(1.1);
  }
  
  .sidebar a > P {
    display: flex;
    align-items: center;
  }
  .sidebar a > p > svg {
    margin-right: 0.5rem;
  }
  
  .MuiTypography-root {
    background-color: #fff !important;
  }